@use '../../mixins.scss' as mixins;

.header {
  padding-top: 32px;
  padding-bottom: 32px;
  background-color: var(--color-primary);
  position: fixed;
  width: 100%;
  z-index: 1;

  .container {
    display: flex;
    justify-content: space-between;

    @include mixins.small-viewport;
    @include mixins.mini-viewport;
    @include mixins.medium-viewport;
    @include mixins.large-viewport;
    @include mixins.ultra-large-viewport;
    align-items: center;

    .authWrapp {
      display: flex;
      justify-content: center;
      margin-right: 80px;
    }

    .authAndChangeThemeWrapp {
      display: flex;
      justify-content: center;
    }

    .menu,
    .menuIcon {
      display: none;
    }

    @media (max-width: 768px) {
      .authAndChangeThemeWrapp {
        display: none;
      }

      .menuIcon {
        display: block;
      }
    }
  }
}
